:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

* {
  box-sizing: border-box;
}

p, h1, h2 {
  margin: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  min-height: 80px;
  background-color: #00f;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.gallery {
  padding: 0 15px;
}

[name="searchQuery"] {
  height: 50px;
  opacity: .8;
  border-radius: 5px;
  margin-right: 10px;
  padding: 0 20px;
}

[type="submit"] {
  height: 50px;
  background-color: #40e0d0;
  border-radius: 5px;
  outline: none;
  padding: 0 20px;
  font-weight: 600;
}

[type="submit"]:hover {
  background-color: #afca4d;
}

.gallery {
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 12px;
  display: grid;
}

.gallery__item {
  background-color: #fff;
  border-radius: 10px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__item:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 200px;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
  display: block;
}

.info {
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 10px;
  padding: 10px 30px;
  display: flex;
}

.info-item {
  min-width: 45%;
}

